.gallery
    position: relative
    width: 100%
    height: 100%
    padding: 32px
    border-radius: 16px
    // display: flex
    // align-items: flex-start
    // justify-content: space-around
    display: grid
    // grid-template-columns: repeat(auto-fill, minmax(200px, 1fr))
    grid-template-columns: repeat(14, 1fr)
    grid-template-rows: repeat(6, 1fr)
    grid-gap: 16px
    z-index: 99
