.navigation_button
    position: relative
    flex-grow: 0
    flex-basis: 15%
    border: none
    margin: 16px 30px
    cursor: pointer
    transition: 2s
    overflow: visible

    &::before
        opacity: 0
        content: ""
        display: block
        position: absolute
        border-radius: 20px 0
        z-index: 1
        top: 2px
        bottom: 2px
        left: 2px
        right: 2px
        background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
        background-size: 100vw 100%
        background-position-x: -400%
        transition: .3s linear
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1)

    &--active

        &::before
            opacity: 1
            top: -1px
            bottom: -1px
            left: -1px
            right: -1px
            transform: skew(4deg, 1.5deg)

    &--first

        &::before
            background-position-x: 0%

    &--second

        &::before
            background-position-x: 25%
    &--third

        &::before
            background-position-x: 50%

    &--fourth

        &::before
            background-position-x: 75%

    &--fifth

        &::before
            background-position-x: 100%

    &__button
        position: absolute
        padding: 0
        width: 100%
        height: 100%
        top: 0
        left: 0
        color: white
        font-size: 3vh
        border: none
        background-color: transparent
        border-radius: 18px 0
        cursor: pointer
        overflow: hidden

        &::before
            position: absolute
            top: 0
            left: 0
            z-index: 2
            width: 100%
            height: 100%
            content: ""
            display: block
            transition: .2s linear
            border-radius: 18px 0
            // background: linear-gradient(90deg, rgb(62, 62, 62), rgb(62, 62, 62))
            background-image: radial-gradient(#414141, #353535)
            background-clip: padding-box
            border: 1px solid transparent

            // box-shadow: inset 0 0 1em #333333

        &::after
            position: absolute
            z-index: 3
            // transform: rotate(-45deg)
            top: -20vh
            left: 50%
            transform: translateX(-50%)
            width: 0%
            height: 0%
            border-radius: 50%
            content: ""
            display: block
            transition: .5s
            // background-image: linear-gradient(90deg, var(--mainColor1) 15%, var(--mainColor2) 85%)
            background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
            background-size: 100vw 100%

        &--first
            &::after
                background-position-x: 0%

        &--second
            &::after
                background-position-x: 25%

        &--third
            &::after
                background-position-x: 50%

        &--fourth
            &::after
                background-position-x: 75%

        &--fifth
            &::after
                background-position-x: 100%

        &_text
            position: absolute
            display: block
            z-index: 30
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
            background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
            background-size: 100vw 100%
            background-position-x: -400%

            background-clip: text
            color: transparent
            transition: .5s

            &--first
                background-position-x: 0%
            &--second
                background-position-x: 25%
            &--third
                background-position-x: 50%
            &--fourth
                background-position-x: 75%
            &--fifth
                background-position-x: 100%

        &:hover
            span
                color: var(--almostWhite)
            &::after
                height: 40vh
                width: 40vh

        &--butter
            flex-grow: 0
            &:hover
                span
                    color: var(--almostWhite)
            &::after, &::before
                opacity: 0
